
import 'swiper/dist/css/swiper.css';
import { swiper, swiperSlide } from 'vue-awesome-swiper';
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'
import Countup from 'vux/src/components/countup/index.vue'

import Swiper from 'swiper'
export default {
name:'ProjectManagerIndex',
components: {swiper,swiperSlide,Marquee, MarqueeItem, Countup},
data(){
return{
swiperOption: {
slidesPerView: 'auto',
spaceBetween: 25,
centeredSlides: true,
loop: true,
pagination: {
el: '.swiper-pagination',
clickable: true,
},
},
pruductpctFlow: [],
isprogressbar: true,
progress3: 0.5,
asyncCount: 0
}
},
methods:{
goProjectManager(){
this.$router.push({name:'ProjectManager'})
},
goProjectSetting(){
this.$router.push({name:'ProjectSetting'})
},
goProjectInfo(){
this.$router.push({name:'ProjectInfo'})
},
goProjectSchedule(){
this.$router.push({name:'ProjectSchedule'})
},
goExpenditureCost(){
this.$router.push({name:'ExpenditureCost'})
},
goProjectCareful(){
this.$router.push({name:'ProjectCareful'})
},
toPoint(data){
if(data){
return (data.replace("%", "")*1).toFixed(1);
}else{
return  0
}
},
/**@name   */
getProductManager_pruductpctFlow(){
this.$vux.loading.show();
this.$http.get(this.$conf.env.getProductManager_pruductpctFlow).then( res =>{
this.pruductpctFlow = res.data;
this.$vux.loading.hide()
}).catch(err =>{
this.$vux.loading.hide()
this.$vux.toast.text(err.response?err.response.data:'')
})
},
/**@name */
getSystem_info(){
this.$http.get(this.$conf.env.getSystem_info).then( res =>{
this.asyncCount = res.data.results;
this.$vux.loading.hide()
}).catch(err =>{
this.$vux.loading.hide()
this.$vux.toast.text(err.response?err.response.data:'')
})
},
},
computed:{
},
mounted(){
this.$emit('setIndexHeaderTitle',{flag:true,title: ''})
this.getProductManager_pruductpctFlow();
this.getSystem_info();
},
destroyed(){
this.$emit('setHeaderShow', {blod:false,title:''})
}
}



